@import '../../../../../style/variables.scss';
@import '../../../../../style/mixins.scss';

.ResumeSectionView-container {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  padding: 0 10px;

  @include crossBrowser(transition, all 0.5s ease);

  @media (max-width: $mediaSmallDesktops) {
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
    -ms-flex-direction: column;
    flex-direction: column;
  }
  @media (max-width: $mediaTablets) {
    padding: 0px 25px;
  }
}

.ResumeSectionView-title {
  -webkit-box-flex: 25%;
  -ms-flex: 25%;
  flex: 25%;

  &__element {
    display: inline-block;
  }

  @media (max-width: $mediaSmallDesktops) {
    margin-bottom: 35px;
  }
}

.resume-title {
  position: relative;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;

  &__item {
    font-weight: 700;
    font-size: 24px;
    font-style: italic;
    text-transform: uppercase;
    text-shadow: 3px 3px 3px rgb(124 49 173 / 50%);
    cursor: pointer;

    @include crossBrowser(transition, all 0.8s ease);

    &:hover {
      text-shadow: 4px 4px 3px rgba(124, 49, 173, 0.9);
      -webkit-animation: text-animate 0.6s;
      animation: text-animate 0.6s;
    }

    @media (max-width: $mediaSmallDesktops) {
      font-size: 28px;
    }
  }
  &:after {
    content: '';
    position: absolute;
    bottom: -15px;
    left: 0;
    right: 0;
    width: 100%;
    height: 2px;
    border: 2px solid rgb(124 49 173);
    border-radius: 5px;
    -webkit-box-shadow: 0 0 5px rgba(0, 0, 0, 0.8);
    box-shadow: 0 0 5px rgba(0, 0, 0, 0.8);
    background-color: #fff;
    background-image: url('https://www.transparenttextures.com/patterns/3px-tile.png');

    -webkit-animation: animate-SkillsBar 60s infinite;

    animation: animate-SkillsBar 60s infinite;
  }
}

.resume-title-hidden {
  color: transparent;
  text-shadow: none;

  &:hover {
    text-shadow: none;
  }
}

.ResumeSectionView-content {
  -webkit-box-flex: 75%;
  -ms-flex: 75%;
  flex: 75%;
}

@-webkit-keyframes animate-title {
  100% {
    background-position: -10000px;
  }
}

@keyframes animate-title {
  100% {
    background-position: -10000px;
  }
}
