@import '../../../../style/variables.scss';
@import '../../../../style/mixins.scss';

.Projects-section {
  position: relative;
  text-align: center;
  background: #191919;
  padding: 150px 0px;
  @include crossBrowser(transition, all 1s ease);
}

.Projects-title-container {
  position: relative;
  display: inline-block;
  @include crossBrowser(transition, all 1s ease);

  .Projects-title {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;

    &__item {
      font-size: 100px;
      text-transform: uppercase;
      font-weight: 700;
      color: #191919;
      text-shadow: 0px 1px 0 rgb(144, 15, 230), 0px -1px 0 rgb(144, 15, 230),
        1px 0px 0 rgb(144, 15, 230), -1px 0px 0 rgb(144, 15, 230),
        1px 1px 0 rgb(144, 15, 230), 1px -1px 0 rgb(144, 15, 230),
        -1px 1px 0 rgb(144, 15, 230), -1px -1px 0 rgb(144, 15, 230);

      cursor: pointer;

      @include crossBrowser(transition, all 0.7s ease);

      &:hover {
        color: #ffffff;

        @include crossBrowser(transform, translateY(-7px) translateX(-7px));

        text-shadow: 6px 6px 0 rgb(144, 15, 230), 0px -1px 0 rgb(144, 15, 230),
          1px 0px 0 rgb(144, 15, 230), -1px 0px 0 rgb(144, 15, 230),
          1px 1px 0 rgb(144, 15, 230), 1px -1px 0 rgb(144, 15, 230),
          -1px 1px 0 rgb(144, 15, 230), -1px -1px 0 rgb(144, 15, 230);
      }

      @media (max-width: $mediaSmallDesktops) {
        font-size: 80px;
      }
      @media (max-width: $mediaSmallTablets) {
        font-size: 50px;
      }
      @media (max-width: $mediaPhones) {
        font-size: 40px;
      }
      @media (max-width: $mediaSmallPhones) {
        font-size: 30px;
      }
    }
    .projects-item-hidden {
      text-shadow: none;
      color: transparent;
    }
  }
  &:before {
    content: '';
    display: inline-block;
    position: absolute;
    top: -50px;
    left: 0;
    right: 0;
    width: 100%;
    height: 3px;
    background-color: rgb(124, 49, 173);

    @media (max-width: $mediaSmallTablets) {
      top: -30px;
    }
    @media (max-width: $mediaPhones) {
      top: -20px;
    }
    @media (max-width: $mediaSmallPhones) {
      top: -15px;
    }
  }
  &:after {
    content: '';
    position: absolute;
    bottom: -55px;
    left: 0;
    right: 0;
    width: 100%;
    height: 3px;
    background-color: rgb(124, 49, 173);

    @media (max-width: $mediaSmallTablets) {
      bottom: -35px;
    }
    @media (max-width: $mediaPhones) {
      bottom: -25px;
    }
    @media (max-width: $mediaSmallPhones) {
      bottom: -18px;
    }
  }
}

.Project-container {
  display: -ms-grid;
  display: grid;
  -ms-grid-columns: 1fr 100px 1fr;
  grid-template-columns: repeat(2, 1fr);
  grid-gap: 100px;
  margin-top: 100px;

  @include crossBrowser(transition, all 1s ease);

  @media (max-width: $mediaSmallTablets) {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    -webkit-box-orient: vertical;
    -webkit-box-direction: normal;
    -ms-flex-direction: column;
    flex-direction: column;
    -webkit-box-align: center;
    -ms-flex-align: center;
    align-items: center;
  }
}
